@font-face {
  font-family: Chewy;
  font-style: normal;
  font-weight: 400;
  src: url("chewy-v18-latin-regular.a9ab7db3.eot");
  src: local(""), url("chewy-v18-latin-regular.a9ab7db3.eot#iefix") format("embedded-opentype"), url("chewy-v18-latin-regular.dfb88645.woff2") format("woff2"), url("chewy-v18-latin-regular.a1e59ddb.woff") format("woff"), url("chewy-v18-latin-regular.4c61beea.ttf") format("truetype"), url("chewy-v18-latin-regular.19cdc4e4.svg#Chewy") format("svg");
}

body {
  --body-background-color: #befcff;
  --body-text-color: #000;
  --head-background-color: #3fdcff;
  --head-text-color: #000;
  --head-text-copyright-color: #444;
  --border: 2px solid black;
  background-color: var(--body-background-color);
  width: 100%;
  font-family: Chewy, Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
}

main {
  min-height: 720px;
}

header, footer {
  background-color: var(--head-background-color);
  color: var(--head-text-color);
}

.carousel-indicators [data-bs-target] {
  width: 10px;
  height: 10px;
  background-color: var(--head-text-color);
  border-radius: 50%;
}

#logo {
  width: 30vw;
  max-width: 205px;
  display: inline-block;
}

#header-1-row {
  width: 100%;
}

header ul li, footer ul li {
  font-size: 1.3rem;
}

.nav-link {
  color: var(--head-text-color);
}

main {
  color: var(--body-text-color);
  text-align: center;
  width: 80%;
  margin: 0 auto;
}

h1, #game-title {
  margin: 2rem;
  font-size: 2rem;
}

#homepage #games {
  flex-wrap: wrap;
  justify-content: space-evenly;
  display: flex;
}

#homepage .game-block {
  height: 360px;
  border-radius: 15px;
  margin-bottom: 1.8rem;
  position: relative;
  overflow: hidden;
}

#homepage .game-block img {
  height: 100%;
  backface-visibility: hidden;
  transition: all .5s;
}

#homepage .middle {
  opacity: 0;
  color: var(--body-background-color);
  transition: all .5s;
  position: absolute;
  bottom: 0;
  left: 50%;
}

#homepage .middle .text {
  font-size: 1.6rem;
  transform: translate(-50%, 30%);
}

#homepage .game-block:hover .image {
  filter: brightness(75%);
  transform: scale(1.1);
}

#homepage .game-block:hover .middle {
  opacity: 1;
  transform: translate(0%, -40%);
}

#game-view {
  grid-template-columns: 1fr 1fr;
  grid-auto-columns: repeat(auto-fit, 350px);
  gap: 10px;
  display: grid;
}

#game-view #game-title {
  text-align: left;
  grid-area: 1 / 1 / auto / 3;
  font-size: 2rem;
}

#game-view #game-description {
  text-align: left;
  grid-area: 2 / 1;
  margin-left: 2rem;
}

#game-view #game-links {
  grid-area: 2 / 2;
  margin: 10px;
  display: block;
}

#game-links * {
  margin: 5px;
}

#game-view #game-screenshots {
  grid-area: 3 / 1 / auto / 3;
}

@media (max-width: 800px) {
  main #game-view {
    display: block;
  }

  #game-view #game-title, #game-description {
    text-align: center;
  }
}

#game-view .img-responsive {
  width: 300px;
  max-width: 100%;
  margin: 10px;
}

#contacts .contactsBlock {
  background-color: var(--head-background-color);
  text-align: center;
  border-radius: 15px;
  justify-content: center;
  align-items: baseline;
  margin-bottom: 2rem;
  display: flex;
}

#contacts .contactsContent {
  text-align: left;
  text-align: left;
  width: -moz-fit-content;
  width: fit-content;
  margin: 3rem;
  line-height: 3rem;
  display: block;
}

#contacts .link-item * {
  color: var(--head-text-color);
  text-decoration: none;
}

#contacts svg {
  height: 2rem;
  fill: var(--head-text-color);
}

@media (max-width: 420px) {
  #contacts .link-item * {
    text-align: center;
    margin: auto;
    display: block;
  }
}

#privacy-policy h1 {
  text-align: center;
}

#privacy-policy {
  text-align: left;
}

#about-us h1 {
  text-align: center;
}

#about-us {
  text-align: left;
}

#error .er-info {
  color: var(--body-text-color);
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

#error h1 {
  font-size: 7vw;
}

#error p {
  font-size: 3.5vw;
}

#error #er-image img {
  min-width: 200px;
  max-width: 30vw;
}

footer {
  width: 100%;
  grid-template-columns: 1fr 3fr;
  padding: 10px 7vw;
  display: grid;
}

.footer-info {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  display: flex;
}

footer ul {
  flex-wrap: wrap;
  justify-content: flex-start;
  column-gap: 1rem;
  padding-left: 0;
  display: flex;
}

footer ul li {
  font-size: 1rem;
  display: inline;
}

footer .copyright {
  color: var(--head-text-copyright-color);
  font-size: .7rem;
}

@media (max-width: 520px) {
  footer {
    text-align: center;
    display: block;
  }

  .footer-info {
    align-items: center;
  }

  footer ul {
    justify-content: center;
  }
}

